@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@300;400;600&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Itim&display=swap);
:root {
    --title: "Itim", cursive;
    --text: "Nunito", sans-serif;
    --dark: #1e1b18;
    --gray: #e0e0e0;
    --light: #fffaff;
    --primary: #ff5d73;
    --primary-dark: #ff334e;
    --accept: #3bc14a;
    --alert: #f94144;
    --padding: 0.4rem;
    --margin: 2rem;
    --radius: 0.2rem;
    --time: 400ms;
    --shadow: 1px 1px 5px 1px #21252938;
}

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

body {
    font-family: "Nunito", -apple-system, BlinkMacSystemFont, "Segoe UI",
        "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
        "Helvetica Neue", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

.App {
    display: grid;
    grid-template-columns: 3fr 7fr;
    grid-template-rows: 100vh;
}

@media screen and (max-width: 1280px) {
    .App {
        grid-template-columns: 1fr;
        grid-template-rows: auto auto;
    }
}

.button {
    background-color: var(--light);
    border-radius: var(--radius);
    border: none;
    color: var(--primary);
    font: 1.6rem var(--text);
    padding: var(--padding);
    transition: all var(--time);
    width: 100%;
}

.button:hover {
    color: var(--primary-dark);
    box-shadow: var(--shadow);
    cursor: pointer;
}

.input {
    align-items: center;
    display: flex;
    flex-direction: column;
    margin: var(--margin) 0;
    width: 100%;
}

.input__label {
    color: var(--light);
    font: 1.4rem var(--text);
    margin-bottom: var(--margin);
}

.input__field {
    background-color: var(--light);
    border: none;
    border-radius: var(--radius);
    color: var(--dark);
    font: 1.2rem var(--text);
    padding: var(--padding);
    transition: all var(--time);
    width: 100%;
}

.input__field:focus {
    box-shadow: var(--shadow);
    outline: none;
}

.input__field:hover {
    box-shadow: var(--shadow);
}

.form {
    width: 100%;
}

.checkbox {
    display: flex;
    margin: var(--margin) 0;
}

.checkbox__label {
    color: var(--light);
    font: 1.4rem var(--text);
    margin-left: var(--margin);
}

.checkbox__field {
    background-color: var(--gray);
    width: 30px;
    height: 30px;
    border-radius: var(--radius);
    transition: all var(--time);
}

.checkbox__field:hover {
    cursor: pointer;
}

.checkbox__checked {
    height: 20px;
    width: 30px;
    border-left: 6px solid var(--accept);
    border-bottom: 6px solid var(--accept);
    -webkit-transform: rotate(-45deg);
            transform: rotate(-45deg);
}

.search {
    margin: var(--margin) 0;
    width: 100%;
}

.menu {
    align-items: center;
    background-color: var(--primary);
    display: flex;
    flex-direction: column;
    padding: var(--padding);
    box-shadow: var(--shadow);
    overflow-y: auto;
}

.logo {
    color: var(--light);
    font: 2.2rem var(--text);
}

.menu--forms {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    flex-grow: 1;
    width: 80%;
}

.trash-can {
    height: 30px;
    width: 30px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
}
.lid {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.lid__top {
    height: 2px;
    width: 10px;
    background-color: var(--gray);
}

.lid__bottom {
    height: 6px;
    width: 25px;
    background-color: var(--gray);
}

.trash {
    height: 20px;
    width: 20px;
    background-color: var(--gray);
}

.trash-can:hover {
    cursor: pointer;
}

.note {
    display: flex;
    justify-content: space-between;
    border-bottom: 3px solid var(--primary);
    margin-bottom: var(--margin);
    padding: var(--padding);
    align-items: center;
}

.note__text {
    font: 1.2rem var(--text);
    color: var(--dark);
    padding: var(--padding);
}

.note__options {
    display: flex;
    align-items: center;
}

.note-list {
    padding: var(--padding);
    overflow-y: scroll;
}

